<template>
    <div class="flow-view" ref="flowview">
        <button class="button" @click="onClick">导出</button>
    </div>
</template>

<script>
import LogicFlow from '@logicflow/core';
import { Menu, DndPanel, SelectionSelect } from "@logicflow/extension";
import "@logicflow/extension/lib/style/index.css";
import "@logicflow/core/dist/style/index.css";

import Approve from './approve';
import End from './end';
import Start from './start';
import Gateway from './gateway';

import graphJSON from './graphData.json'
// const graphJSON = require('./graphData.json')

// fetch('./graphData.json').then(res => {
//     console.log('res :>> ', res);
// })

console.log('graphJSON :>> ', graphJSON);

LogicFlow.use(Menu);
LogicFlow.use(DndPanel);
LogicFlow.use(SelectionSelect);

let lf = undefined

export default {
    name: 'LogicFlow',
    data() {
        return {
            className: ''
        }
    },
    mounted() {
        lf = new LogicFlow({
            container: this.$refs.flowview,
            grid: true,
            keyboard: {
                enabled: true
            }
        })
        lf.register(Approve)
        lf.register(End)
        lf.register(Start)
        lf.register(Gateway)

        lf.extension.menu.addMenuConfig({
            graphMenu: [
                {
                    text: "打印",
                    callback: () => {
                        console.log('graphModel :>> ', JSON.stringify(lf.getGraphData()));
                    },
                }
            ]
        })

        lf.extension.dndPanel.setPatternItems([
            {
                label: '选区',
                icon: 'data:image/png;base64,',
                callback: () => {
                    lf.extension.selectionSelect.openSelectionSelect();
                    lf.once('selection:selected', () => {
                        lf.extension.selectionSelect.closeSelectionSelect();
                    });
                    this.className = 'dnd-item-active'
                    console.log('lf.extension.dndPanel :>> ', lf.extension.dndPanel.panelEl.firstChild);
                },
                className: `dnd-item-custom ${this.className}`
            },
            {
                type: 'start',
                text: '开始',
                label: '开始节点',
                icon: 'data:image/png;base64,',
            },
            {
                type: 'end',
                text: '结束',
                label: '结束节点',
                icon: 'data:image/png;base64,',
                className: 'dnd-item-custom'
            },
            {
                type: 'gateway',
                label: '条件判断',
                icon: 'data:image/png;base64,',
                className: 'dnd-item-custom'
            },
            {
                type: 'approve',
                label: '审批节点',
                icon: 'data:image/png;base64,',
                className: 'important-node'
            },
            {
                type: 'rect',
                label: '系统任务',
                icon: 'data:image/png;base64,',
                className: 'import_icon'
            },
        ])

        lf.render(graphJSON)
    },
    methods: {
        onClick() {
            console.log('data :>> ', JSON.stringify(lf.getGraphData()));
            alert('已导出json，控制台查看')
        }
    },
}

</script>

<style>
.button {
    position: fixed;
    right: 30px;
    top: 30px;
}

.flow-view {
    min-width: 100vm;
    height: 100vh;
}

.dnd-item-custom {
    padding-bottom: 8px;
    border-bottom: 1px solid #aaa;
}

.dnd-item-custom .lf-dnd-shape {
    cursor: pointer;
}

.dnd-item-active .lf-dnd-shape {
    background-color: #333;
}
</style>
